"use client";

import { useState, useEffect, type FormEvent, type ChangeEvent } from "react";
import { useRouter } from "next/navigation";
import clsx from "clsx";

import { createProduct } from "@/lib/services/productService";
import { createPromo } from "@/lib/services/promoService";

import type {
  Producto,
  Promo,
  Tokens,
} from "@/lib/types";

/* -------------------------------------------------------------------------- */
/*                                   Tipos                                    */
/* -------------------------------------------------------------------------- */

type PromoType = "descuento" | "2x1" | "nuevo" | "solo-hoy";

interface TextareaProps {
  label: string;
  value: string;
  onChange: (value: string) => void;
}

interface InputCommaProps {
  label: string;
  placeholder: string;
  value: string;
  onChange: (value: string) => void;
}

interface PromoFormProps {
  tipo: PromoType;
  valor: number;
  nombrePromo: string;
  descPromo: string;
  diasSemana: number[];
  msg: { ok?: string; err?: string };
  toggleDia: (d: number) => void;
  setTipo: (v: PromoType) => void;
  setValor: (v: number) => void;
  setNombrePromo: (v: string) => void;
  setDescPromo: (v: string) => void;
  handleSubmitPromo: (e: FormEvent) => Promise<void>;
}

interface InputTextProps {
  label: string;
  value: string;
  onChange: (value: string) => void;
  placeholder?: string;
}

const TAG_OPTIONS = [
  "bebida",
  "frío",
  "dulce",
  "panadería",
  "refresco",
  "snack",
  "krusty",
  "salado",
  "chicle",
  "revista",
  "lectura",
  "caliente",
  "comida",
];

const DIAS_SEMANA = [
  "Domingo",
  "Lunes",
  "Martes",
  "Miércoles",
  "Jueves",
  "Viernes",
  "Sábado",
];

/* -------------------------------------------------------------------------- */
/*                             Reloj digital LED                              */
/* -------------------------------------------------------------------------- */

function DigitalClock() {
  const [now, setNow] = useState(new Date());
  useEffect(() => {
    const id = setInterval(() => setNow(new Date()), 1000);
    return () => clearInterval(id);
  }, []);
  const dia = DIAS_SEMANA[now.getDay()];
  const [h, m, s] = [
    now.getHours(),
    now.getMinutes(),
    now.getSeconds(),
  ].map((n) => n.toString().padStart(2, "0"));

  return (
    <div
      className="select-none rounded-2xl border-4 border-amber-800 bg-black shadow-2xl"
      style={{ width: 300, height: 170, boxShadow: "0 0 30px rgba(100,77,50,.33)" }}
    >
      <p
        className="mt-2 mb-3 text-center text-2xl font-extrabold tracking-wider text-blue-200 uppercase"
        style={{
          textShadow: "0 1px 6px #60a5fa,0 2px 10px #0ea5e9",
          letterSpacing: ".16em",
        }}
      >
        {dia}
      </p>
      <p
        className="text-center font-mono font-bold text-green-400 text-[2.5rem] md:text-[3rem]"
        style={{
          textShadow: "0 0 22px #22d3ee,0 0 4px #14b8a6,0 0 2px #0f766e",
          letterSpacing: ".17em",
        }}
      >
        {h}:{m}:{s}
      </p>
    </div>
  );
}

/* -------------------------------------------------------------------------- */
/*                               Componente UI                                */
/* -------------------------------------------------------------------------- */

export default function AdminCrearPage() {
  const router = useRouter();

  /* ------------------------- Selector de formulario ------------------------ */
  const [form, setForm] = useState<"producto" | "promo">("producto");

  /* ------------------------- Estado — Producto ----------------------------- */
  const [previewSrc, setPreviewSrc] = useState<string>();
  const [file, setFile] = useState<File>();
  const [nombre, setNombre] = useState("");
  const [precio, setPrecio] = useState("");
  const [etiquetas, setEtiquetas] = useState<string[]>([]);
  const [descripcion, setDescripcion] = useState("");
  const [sabores, setSabores] = useState("");
  const [tamanos, setTamanos] = useState("");
  const [msgProd, setMsgProd] = useState<{ ok?: string; err?: string }>({});

  const handleFile = (e: ChangeEvent<HTMLInputElement>) => {
    const f = e.target.files?.[0];
    if (!f) return;
    setFile(f);
    setPreviewSrc(URL.createObjectURL(f));
  };
  const toggleTag = (tag: string) =>
    setEtiquetas((prev) =>
      prev.includes(tag) ? prev.filter((t) => t !== tag) : [...prev, tag]
    );

  const handleSubmitProducto = async (e: FormEvent) => {
    e.preventDefault();
    setMsgProd({});
    if (!file) return setMsgProd({ err: "Debes subir una imagen." });
    if (!nombre.trim()) return setMsgProd({ err: "Nombre es requerido." });
    if (!precio || isNaN(+precio)) return setMsgProd({ err: "Precio inválido." });

    try {
      await createProduct({
        name: nombre,
        price: +precio,
        image: `/images/${file.name}`,
        tags: etiquetas,
        description: descripcion,
        options: {
          flavor: sabores
            .split(",")
            .map((s) => s.trim())
            .filter(Boolean),
          size: tamanos
            .split(",")
            .map((t) => t.trim())
            .filter(Boolean),
        },
      });
      setMsgProd({ ok: "¡Producto creado!" });
      setTimeout(() => router.push("/admin/visualizar"), 1000);
    } catch (err: any) {
      console.error(err);
      setMsgProd({ err: err.message || "Error al crear producto." });
    }
  };

  /* ------------------------- Estado — Promo ------------------------------- */
  const [tipo, setTipo] = useState<PromoType>("descuento");
  const [valor, setValor] = useState(10);
  const [nombrePromo, setNombrePromo] = useState("");
  const [descPromo, setDescPromo] = useState("");
  const [diasSemana, setDiasSemana] = useState<number[]>([]);
  const [msgPromo, setMsgPromo] = useState<{ ok?: string; err?: string }>({});

  const toggleDia = (d: number) =>
    setDiasSemana((prev) =>
      prev.includes(d) ? prev.filter((x) => x !== d) : [...prev, d]
    );

  const handleSubmitPromo = async (e: FormEvent) => {
    e.preventDefault();
    setMsgPromo({});
    if (!nombrePromo.trim() || !descPromo.trim() || diasSemana.length === 0)
      return setMsgPromo({ err: "Completa todos los campos y elige al menos un día." });
    if (tipo === "descuento" && (valor < 1 || valor > 99))
      return setMsgPromo({ err: "Descuento entre 1% y 99%." });

    try {
      await createPromo({
        tipo,
        valor: tipo === "descuento" ? valor : undefined,
        nombre: nombrePromo,
        descripcion: descPromo,
        diasSemana: diasSemana.sort(),
      });
      setMsgPromo({ ok: "¡Promoción creada!" });
      setTimeout(() => router.push("/admin/visualizar"), 1000);
    } catch (err: any) {
      console.error(err);
      setMsgPromo({ err: err.message || "Error al crear promoción." });
    }
  };

  /* ---------------------------- Render ------------------------------------ */
  return (
    <section className="relative min-h-[700px] p-6 pb-22">
      <div className="mx-auto flex max-w-7xl flex-col gap-8 md:flex-row md:items-stretch">
        <div className="flex flex-1 flex-col items-center">
          {/* Selector tabs */}
          <div className="mb-8 flex w-full max-w-lg gap-4">
            {["producto", "promo"].map((t) => (
              <button
                key={t}
                onClick={() => setForm(t as "producto" | "promo")}
                className={clsx(
                  "flex-1 rounded-t-lg border-b-4 px-6 py-2 font-bold transition",
                  form === t
                    ? t === "producto"
                      ? "bg-yellow-400 border-yellow-700 text-yellow-900"
                      : "bg-blue-400 border-blue-700 text-blue-900"
                    : "bg-white border-gray-200 text-gray-600 hover:bg-gray-100"
                )}
              >
                {t === "producto" ? "Crear Producto" : "Crear Promoción"}
              </button>
            ))}
          </div>

          <div className="flex w-full justify-center">
            {form === "producto" ? (
              <ProductoForm
                {...{
                  previewSrc,
                  nombre,
                  precio,
                  etiquetas,
                  descripcion,
                  sabores,
                  tamanos,
                  handleFile,
                  toggleTag,
                  setNombre,
                  setPrecio,
                  setDescripcion,
                  setSabores,
                  setTamanos,
                  handleSubmitProducto,
                  msgProd,
                }}
              />
            ) : (
              <PromoForm
                {...{
                  tipo,
                  valor,
                  nombrePromo,
                  descPromo,
                  diasSemana,
                  msg: msgPromo,
                  toggleDia,
                  setTipo,
                  setValor,
                  setNombrePromo,
                  setDescPromo,
                  handleSubmitPromo,
                }}
              />
            )}
          </div>
        </div>

        <div className="hidden min-w-[190px] md:flex md:w-[25%] flex-col items-center justify-center">
          <DigitalClock />
        </div>
      </div>

      <div className="mt-12 flex justify-center md:hidden">
        <DigitalClock />
      </div>
    </section>
  );
}

/* -------------------------------------------------------------------------- */
/*                            Forms & Helpers UI                             */
/* -------------------------------------------------------------------------- */

function ProductoForm(props: any) {
  const {
    previewSrc,
    nombre,
    precio,
    etiquetas,
    descripcion,
    sabores,
    tamanos,
    handleFile,
    toggleTag,
    setNombre,
    setPrecio,
    setDescripcion,
    setSabores,
    setTamanos,
    handleSubmitProducto,
    msgProd,
  } = props;
  return (
    <div className="mb-12 w-full max-w-2xl rounded-lg bg-white p-8 shadow-lg">
      <h1 className="mb-6 text-2xl font-bold">Agregar Nuevo Producto</h1>
      {msgProd.err && <p className="text-red-500 mb-2">{msgProd.err}</p>}
      {msgProd.ok && <p className="text-green-600 mb-2">{msgProd.ok}</p>}
      <form onSubmit={handleSubmitProducto} className="space-y-6">
        {/* Nombre + precio */}
        <div className="grid grid-cols-1 gap-4 md:grid-cols-4">
          <div className="md:col-span-3">
            <label className="mb-1 block font-semibold">Nombre</label>
            <input
              value={nombre}
              onChange={(e) => setNombre(e.target.value)}
              required
              className="w-full rounded border-2 border-green-300 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-green-400"
            />
          </div>
          <div>
            <label className="mb-1 block font-semibold">Precio</label>
            <input
              type="number"
              step="0.01"
              value={precio}
              onChange={(e) => setPrecio(e.target.value)}
              required
              className="w-full rounded border-2 border-green-300 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-green-400"
            />
          </div>
        </div>

        {/* Tags */}
        <TagSelector etiquetas={etiquetas} toggleTag={toggleTag} />

        {/* Descripción */}
        <Textarea
          label="Descripción"
          value={descripcion}
          onChange={setDescripcion}
        />

        {/* Opciones */}
        <div className="grid grid-cols-1 gap-4 md:grid-cols-2">
          <InputComma
            label="Sabores"
            placeholder="Cereza, Uva, Cola"
            value={sabores}
            onChange={setSabores}
          />
          <InputComma
            label="Tamaños"
            placeholder="Pequeño, Mediano, Grande"
            value={tamanos}
            onChange={setTamanos}
          />
        </div>

        {/* Imagen */}
        <div>
          <label className="mb-1 block font-semibold">Imagen</label>
          <input
            type="file"
            accept="image/*"
            onChange={handleFile}
            required
            className="mb-2 block"
          />
          {previewSrc && (
            <img
              src={previewSrc}
              alt="preview"
              className="h-40 w-40 rounded border object-cover"
            />
          )}
        </div>

        <button
          type="submit"
          className="w-full rounded-lg bg-green-500 py-2 font-semibold text-white transition hover:bg-green-600"
        >
          Guardar Producto
        </button>
      </form>
    </div>
  );
}

function TagSelector({
  etiquetas,
  toggleTag,
}: {
  etiquetas: string[];
  toggleTag: (t: string) => void;
}) {
  return (
    <div>
      <label className="mb-2 block font-semibold">Categorías</label>
      <div className="flex flex-wrap gap-2">
        {TAG_OPTIONS.map((tag) => (
          <button
            key={tag}
            type="button"
            onClick={() => toggleTag(tag)}
            className={clsx(
              "rounded-full border px-4 py-1 transition",
              etiquetas.includes(tag)
                ? "bg-green-400 border-green-600 text-white"
                : "bg-white border-gray-300 text-gray-800 hover:bg-gray-100"
            )}
          >
            {tag.charAt(0).toUpperCase() + tag.slice(1)}
          </button>
        ))}
      </div>
    </div>
  );
}

const Textarea = ({ label, value, onChange }: TextareaProps) => (
  <div>
    <label className="mb-1 block font-semibold">{label}</label>
    <textarea
      value={value}
      onChange={(e) => onChange(e.target.value)}
      className="h-24 w-full rounded border-2 border-green-300 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-green-400"
    />
  </div>
);

const InputComma = ({
  label,
  placeholder,
  value,
  onChange,
}: InputCommaProps) => (
  <div>
    <label className="mb-1 block font-semibold">
      {label} <span className="text-sm text-gray-500">(coma separados)</span>
    </label>
    <input
      value={value}
      onChange={(e) => onChange(e.target.value)}
      placeholder={placeholder}
      className="w-full rounded border-2 border-green-300 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-green-400"
    />
  </div>
);

function PromoForm({
  tipo,
  valor,
  nombrePromo,
  descPromo,
  diasSemana,
  msg,
  toggleDia,
  setTipo,
  setValor,
  setNombrePromo,
  setDescPromo,
  handleSubmitPromo,
}: PromoFormProps) {
  return (
    <div className="w-full max-w-xl rounded-lg bg-white p-8 shadow-lg">
      <h1 className="mb-6 text-2xl font-bold">Crear Nueva Promoción</h1>
      {msg.err && <p className="text-red-500 mb-2">{msg.err}</p>}
      {msg.ok && <p className="text-green-600 mb-2">{msg.ok}</p>}
      <form onSubmit={handleSubmitPromo} className="space-y-6">
        <div className="grid grid-cols-1 gap-4 md:grid-cols-2">
          <div>
            <label className="mb-1 block font-semibold">Tipo</label>
            <select
              value={tipo}
              onChange={(e) => {
                const v = e.target.value as PromoType;
                setTipo(v);
                if (v !== "descuento") setValor(0);
              }}
              className="w-full rounded border-2 border-blue-300 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-400"
            >
              <option value="descuento">Descuento %</option>
              <option value="2x1">2x1</option>
              <option value="nuevo">Nuevo</option>
              <option value="solo-hoy">Sólo hoy</option>
            </select>
          </div>
          {tipo === "descuento" && (
            <div>
              <label className="mb-1 block font-semibold">Porcentaje</label>
              <input
                type="number"
                min={1}
                max={99}
                value={valor}
                onChange={(e) => setValor(Number(e.target.value))}
                required
                className="w-full rounded border-2 border-blue-300 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-400"
              />
            </div>
          )}
        </div>

        <InputText
          label="Nombre"
          value={nombrePromo}
          onChange={setNombrePromo}
          placeholder="Ej. 15% Squishee"
        />
        <Textarea
          label="Descripción"
          value={descPromo}
          onChange={setDescPromo}
        />

        <div>
          <label className="mb-1 block font-semibold">Días Semana</label>
          <div className="flex flex-wrap gap-2">
            {DIAS_SEMANA.map((dia, i) => (
              <button
                key={dia}
                type="button"
                onClick={() => toggleDia(i)}
                className={clsx(
                  "rounded-full border px-4 py-1 font-semibold transition",
                  diasSemana.includes(i)
                    ? "bg-blue-400 border-blue-600 text-white"
                    : "bg-white border-gray-300 text-gray-800 hover:bg-gray-100"
                )}
              >
                {dia}
              </button>
            ))}
          </div>
        </div>

        <button
          type="submit"
          className="w-full rounded-lg bg-blue-500 py-2 font-semibold text-white transition hover:bg-blue-600"
        >
          Guardar Promoción
        </button>
      </form>
    </div>
  );
}

const InputText = ({
  label,
  value,
  onChange,
  placeholder,
}: InputTextProps) => (
  <div>
    <label className="mb-1 block font-semibold">{label}</label>
    <input
      value={value}
      onChange={(e) => onChange(e.target.value)}
      placeholder={placeholder}
      required
      className="w-full rounded border-2 border-blue-300 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-400"
    />
  </div>
);
